import * as React from "react";

import Layout from "../components/layout";
import ProjectCard from "../components/ProjectCard";
import EtuSkills from "../images/etuskills_full.svg";
import EVolPass from "../images/evolpass.svg";
import NeoWave from "../images/neowave.svg";
import TriStarEsports from "../images/tristar.svg";
import SpkrGIA from "../images/spkr_gia.svg";
import WSITL from "../images/wsitl.svg";
import LSA from "../images/lsa.svg";

const ProjectsPage = () => (
  <Layout title="Проекты Фонда | ФРИИС">
    <h1 className="page-header">Проекты</h1>
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      <ProjectCard image={EtuSkills} name={"ETUSkills"} href="https://worldskills.iisdf.org/"
                   description={"Фестиваль профессионального мастерства по стандартам WorldSkills"} />
      <ProjectCard image={NeoWave} name={"NeoWave"} href="https://neowave.iisdf.org/"
                   description={"Мультифункциональная система управления образовательной организацией, контроля достижений обучающихся"} />
      <ProjectCard image={EVolPass} name={"Электронное волонтёрство"}
                   description={"Веб-платформа для образовательных организаций для упрощения контроля деятельности волонтёров-обучающихся"} />
      <ProjectCard image={SpkrGIA} name={"СПКР ГИА"}
                   description={"Платформа для генерации бланков различных контрольных работ, тестов (аналогичным ГИА) с их последующей обработкой, автоматической генерацией результатов для тестовой части и полуавтоматической для части с развёрнутым ответом"} />
      <ProjectCard image={TriStarEsports} name={"TriStar Esports"}
                   description={"Оператор киберспортивных турниров в образовательных организациях"} />
      <ProjectCard image={WSITL} name={"СЦК IT-лицея КФУ"} href="https://wsitl.iisdf.org/"
                   description={"Специализированный центр компетенций «Молодые профессионалы IT-лицея КФУ»"} />
      <ProjectCard image={LSA} name={"Платформа Ассоциации лицеистов"} href="https://lsa.iisdf.org/"
                   description={"Платформа создана для публикации и контроля документационной базы, кадрового состава, структуры Ассоциации лицеистов IT-лицея КФУ"} />
    </div>
  </Layout>
);

export default ProjectsPage;
