import * as React from "react";


const ProjectCard = ({ image, name, description, href }) => (
  <div className="border-brand border-2 p-7 rounded-2xl">
    {image && <img className="aspect-video mb-4 max-w-xs mx-auto" src={image} alt={name} />}
    <h4 className="text-2xl text-brand font-bold mb-2">{href &&
      <a href={href} className="link-brand">{name}</a>}{!href && <>{name}</>}</h4>
    {description && <p className="text-sm">{description}</p>}
  </div>
);

export default ProjectCard;
